import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';


const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  
  headers: any;

  constructor(private http:HttpClient) { 
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
  }

  login(user) {
    let userJson = JSON.stringify(user);
    console.log(userJson);
    return this.http.post(BASE_URL+'/login',userJson,{headers:this.headers});
  }

  signup(user) {
    console.log(user);
    return this.http.post(BASE_URL+'/signup',user,{headers:this.headers});
  }
}
