import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { AnnotationService } from '../annotation/annotation.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-review-screen',
  templateUrl: './review-screen.component.html',
  styleUrls: ['./review-screen.component.css']
})
export class ReviewScreenComponent implements OnInit {

  privilege:any;
  articles:any;
  constructor(private service:AnnotationService,
    private location: Location,
    private router:Router
  ) { }

  ngOnInit() {
    this.privilege=localStorage.getItem("privilege");
    var obj = {"user_id":+localStorage.getItem('user_id'),"token":localStorage.getItem("token")};
    this.service.fetchAll(JSON.stringify(obj)).subscribe(
      res=>{
        console.log(res[0]);
        this.articles = res;
      }
    );
  }
  goBack() {
    this.location.back();
  }
  review(id){
    localStorage.setItem('total_page',"0");
    localStorage.setItem('page',"0");
    localStorage.setItem('article_id',id);
    localStorage.setItem('review',"1");
    this.router.navigateByUrl("/annotation");
  }

}
