import { Component, OnInit } from '@angular/core';
import { SupplyService } from '../supply.service';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-supply',
  templateUrl: './supply.component.html',
  styleUrls: ['./supply.component.css']
})
export class SupplyComponent implements OnInit {
  supplies:any;
  addSupply = new FormGroup({
    supply_value: new FormControl("")
  });
  
  constructor(private service:SupplyService) { }

  ngOnInit() {
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchSupply(body).subscribe(
      res => {
        this.supplies = res['valueList'];
      }
    );

  }
  onSubmit(){
    console.log(this.addSupply.value);
    this.addSupply.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addSupply.value);
    this.service.addSupply(bodyJson).subscribe(
      res => {
        if(res['supply_value_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
    
  }
  delete(id,name){
    var obj = {"supply_value_id":id,"status":"disabled","supply_value":name,"token":localStorage.getItem("token")};
    var objJson=JSON.stringify(obj);
    this.service.deleteSupply(objJson).subscribe(
      res=>{
        location.reload();
        console.log(res);
      }
    );
  }

}
