import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};

@Injectable({
  providedIn: 'root'
})
export class FactorValueService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }

   fetchFactorValue(token){
     return this.http.post(BASE_URL+"/fetchfactorvalue",token,{headers:this.headers});
   }
   deleteFactorValue(body){
    return this.http.post(BASE_URL+"/admindeletefactorvalue",body,{headers:this.headers});
   }
   addFactorValue(body){
   return this.http.post(BASE_URL+"/adminaddfactorvalue",body,{headers:this.headers});
   }
}
