import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};
@Injectable({
  providedIn: 'root'
})
export class UserAddService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }
  fetchUsers(body){
    return this.http.post(BASE_URL+"/adminusersfetch",body,{headers:this.headers});
  }
  update(body){
    return this.http.post(BASE_URL+"/adminuseredit",body,{headers:this.headers});
  }
}
