import { Component, OnInit, Input } from '@angular/core';
import { AnnotationService } from '../annotation.service';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-article-info',
  templateUrl: './article-info.component.html',
  styleUrls: ['./article-info.component.css']
})
export class ArticleInfoComponent implements OnInit {
  infoForm = new FormGroup({
    article_id:new FormControl(""),
    headline: new FormControl(""),
    owner: new FormControl(""),
    release_date: new FormControl(""),
    source: new FormControl(""),
    url: new FormControl(""),
  });
  page:number = 1;
  article: any;
  constructor(private service:AnnotationService) { }

  ngOnInit() {
    var token={"token":localStorage.getItem("token")};
    var obj = {"user_id":+localStorage.getItem("user_id"),"flag":this.page,"token":localStorage.getItem("token")};
    console.log(JSON.stringify(obj));
    this.service.fetch(JSON.stringify(obj)).subscribe(
      res=>{
        this.article=res;
        if(this.article['article_id']!=""){
          this.infoForm.controls['article_id'].setValue(this.article['article_id']);
        }
        if(this.article['headline']!=""){
          this.infoForm.controls['headline'].setValue(this.article['headline']);
        }
        if(this.article['owner']!=""){
          this.infoForm.controls['owner'].setValue(this.article['owner']);
        }
        if(this.article['release_date']!=""){
          this.infoForm.controls['release_date'].setValue(this.article['release_date']);
        }
        if(this.article['owner']!=""){
          this.infoForm.controls['owner'].setValue(this.article['owner']);
        }
        if(this.article['url']!=""){
          this.infoForm.controls['url'].setValue(this.article['url']);
        }
      }
    );
    this.saveForm();
  }

  submit(e:any){
    this.saveForm();
  }
  ngOnDestroy(){
    this.saveForm();
  }

  saveForm(){
    localStorage.setItem("article_id",this.infoForm.value.article_id);
    localStorage.setItem("headline",this.infoForm.value.headline);
    localStorage.setItem("owner",this.infoForm.value.owner);
    localStorage.setItem("release_date",this.infoForm.value.release_date);
    localStorage.setItem("source",this.infoForm.value.source);
    localStorage.setItem("url",this.infoForm.value.url);
  }
}
