import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { CategoryService } from '../category.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {
  categories:any;
  addCategory = new FormGroup({
    categories  : new FormControl("")
  });
  
  constructor(private service:CategoryService) { }

  ngOnInit() {
    
    let token = localStorage.getItem('token');
    var body = {"token":token};
    let tokenJson = JSON.stringify(body);
    this.service.fetchCategory(body).subscribe(
      res => {
        this.categories = res['valueList'];
      }
    );

  }
  onSubmit(){
    console.log(this.addCategory.value);
    this.addCategory.value.token=localStorage.getItem('token');
    let bodyJson = JSON.stringify(this.addCategory.value);
    this.service.addCategory(bodyJson).subscribe(
      res => {
        if(res['category_id']){
          console.log("success")
        }
        else{
          console.log("error")
        }
        location.reload();
      }
    );
  }
  delete(id,name){
    var obj = {"category_id":id,"status":"disabled","categories":name,"token":localStorage.getItem("token")};
    var objJson=JSON.stringify(obj);
    this.service.deleteCategory(objJson).subscribe(
      res=>{
        location.reload();
        console.log(res);
      }
    );
  }

}
