import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};
@Injectable({
  providedIn: 'root'
})
export class SupplyService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }

   fetchSupply(token){
     return this.http.post(BASE_URL+"/fetchsupply",token,{headers:this.headers});
   }
   deleteSupply(body){
    return this.http.post(BASE_URL+"/admindeletesupply",body,{headers:this.headers});
   }
   addSupply(body){
   return this.http.post(BASE_URL+"/adminaddsupply",body,{headers:this.headers});
   }
}
