import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};
@Injectable({
  providedIn: 'root'
})
export class UploadService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }
   upload(body){
     return this.http.post(BASE_URL+"/csvupload",body,{headers:this.headers});
   }
   fetch(id,flag){
    return this.http.get(BASE_URL+"/articlecontent?user_id="+id+"&flag="+flag,{headers:this.headers});
   }

}
