import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};
@Injectable({
  providedIn: 'root'
})
export class DemandService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }

   fetchDemand(token){
     return this.http.post(BASE_URL+"/fetchdemand",token,{headers:this.headers});
   }
   deleteDemand(body){
    return this.http.post(BASE_URL+"/admindeletedemand",body,{headers:this.headers});
   }
   addDemand(body){
   return this.http.post(BASE_URL+"/adminaddDemand",body,{headers:this.headers});
   }
}
