import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';

const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};
@Injectable({
  providedIn: 'root'
})
export class AnnotationService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }

  fetch(body){
    return this.http.post(BASE_URL+"/articlecontent",body,{headers:this.headers});
   }

   fetchAll(body){
     return this.http.post(BASE_URL+"/articlereview",body,{headers:this.headers});
   }
   save(body){
    return this.http.post(BASE_URL+"/articlesave",body,{headers:this.headers});
   }
   markIrr(body){
    return this.http.post(BASE_URL+"/markirrelevant",body,{headers:this.headers});
   }
   count(body){
     return this.http.post(BASE_URL+"/annotationcount",body,{headers:this.headers});
   }
   fetchById(body){
    return this.http.post(BASE_URL+"/articlecontentid",body,{headers:this.headers});
   }
}

