import { Component, OnInit, Input } from '@angular/core';
import { AnnotationService } from '../annotation.service';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-article-content',
  templateUrl: './article-content.component.html',
  styleUrls: ['./article-content.component.css']
})
export class ArticleContentComponent implements OnInit {
  contentForm=new FormGroup({
    content:new FormControl(""),
  });
  page:number =1;
  article: any;
  constructor(private service:AnnotationService) { }

  ngOnInit() {
    var token={"token":localStorage.getItem("token")};
    var obj = {"user_id":+localStorage.getItem("user_id"),"flag":this.page,"token":localStorage.getItem("token")};
    this.service.fetch(JSON.stringify(obj)).subscribe(
      res=>{
        this.article=res;
        if(this.article['content']!=""){
          this.contentForm.controls['content'].setValue(this.article['content']);
        }
      }
    );
    var content=this.contentForm.value.content;
        localStorage.setItem("content",content);
  }
  ngOnDestroy(){
    var content=this.contentForm.value.content;
        localStorage.setItem("content",content);
  }
  submit(e:any){
    var content=this.contentForm.value.content;
        localStorage.setItem("content",content);
  }

}
