import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};

@Injectable({
  providedIn: 'root'
})
export class CategoryService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }

   fetchCategory(token){
     return this.http.post(BASE_URL+"/fetchcategory",token,{headers:this.headers});
   }
   deleteCategory(body){
     return this.http.post(BASE_URL+"/admindeletecategory",body,{headers:this.headers});
   }
   addCategory(body){
    return this.http.post(BASE_URL+"/adminaddcategory",body,{headers:this.headers});
   }
}
