import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { LoginService } from './login.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm = new FormGroup({
    email: new FormControl(''),
    password: new FormControl(''),
  });

  userId:any
  userPrivilege:any
  
  constructor(private router:Router, private service: LoginService) { }

  ngOnInit() {
    document.body.classList.add('login-body');
  }
  ngOnDestroy() {
    document.body.classList.remove('login-body');
  }
  onSubmit(data){
    this.service.login(data).subscribe(data => {
       this.userId = data[1]['user_id'];
       this.userPrivilege = data[1]['privilege'];
       localStorage.setItem('token', data[0]['token']);
       localStorage.setItem('user_id', this.userId);
       localStorage.setItem('privilege', this.userPrivilege);
       localStorage.setItem('page',"2");
       if(data[0] == 'Error' || data[0] == 'error' || data == 'Error' || data == 'error') {
         console.log("Internal error")
       } else if(data[0] == 'invalid' || data=='invalid') {
         alert("Incorrect username or password");
       }else if(this.userPrivilege == "1"){
        this.router.navigate(['/index']);
  
      } else if(this.userPrivilege == "2"){
        this.router.navigate(['/upload']);
      }
      else{
        this.router.navigate(['']);
      }
  })
  }

}
