import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { UploadComponent } from './upload/upload.component';
import { AnnotationComponent } from './annotation/annotation.component';
import { ReviewScreenComponent } from './review-screen/review-screen.component';
import { IndexComponent } from './index/index.component';
import { UserAddComponent } from './user-add/user-add.component';
import { RegionComponent } from './region/region.component';
import { CommodityComponent } from './commodity/commodity.component';
import { CategoryComponent } from './category/category.component';
import { SubCategoryComponent } from './sub-category/sub-category.component';
import { MovingFactorComponent } from './moving-factor/moving-factor.component';
import { FactorValueComponent } from './factor-value/factor-value.component';
import { DisruptionComponent } from './disruption/disruption.component';
import { DemandComponent } from './demand/demand.component';
import { SupplyComponent } from './supply/supply.component';
import { PriceComponent } from './price/price.component';

const routes: Routes = [
  {'path':'',component:LoginComponent},
  {'path':'upload',component:UploadComponent},
  {'path':'annotation',component:AnnotationComponent},
  {'path':'review',component:ReviewScreenComponent},
  {'path':'index',component:IndexComponent},
  {'path':'users',component:UserAddComponent},
  {'path':'region',component:RegionComponent},
  {'path':'commodity',component:CommodityComponent},
  {'path':'category',component:CategoryComponent},
  {'path':'subCategory',component:SubCategoryComponent},
  {'path':'movingFactor',component:MovingFactorComponent},
  {'path':'factorValue',component:FactorValueComponent},
  {'path':'price',component:PriceComponent},
  {'path':'supply',component:SupplyComponent},
  {'path':'demand',component:DemandComponent},
  {'path':'disruption',component:DisruptionComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
