import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const BASE_URL = 'http://162.241.222.215';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
  })
};

@Injectable({
  providedIn: 'root'
})
export class CommodityService {
  headers: any;
  constructor(private http:HttpClient) {
    this.headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8','Access-Control-Allow-Origin':'*'});
   }

   fetchCommodity(token){
     return this.http.post(BASE_URL+"/fetchcommodity",token,{headers:this.headers});
   }
   deleteCommodity(body){
     return this.http.post(BASE_URL+"/admindeletecommodity",body,{headers:this.headers});
   }
   addCommodity(body){
    return this.http.post(BASE_URL+"/adminaddcommodity",body,{headers:this.headers});
   }
}
